@keyframes fromright {
    from {
        margin-left: 50em;
    }

    to {
        margin-left: 0em;
    }
}

@keyframes fromleft {
    from {
        margin-left: -50em;
    }

    to {
        margin-left: 0em;
    }
}

@keyframes appear {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.slogan {
    width: 100%;
}

.container h1 {
    color: rgb(49, 52, 57);
    font-size: 1.8em;
    width: 80%;
    text-align: center;
    padding-left: 10%;
    padding-right: 10%;
    animation: fromright;
    animation-duration: 2s;
    margin-top: 3%;
    margin-bottom: 3%;
}


.container h2 {
    font-size: 1.6em;
    color: rgb(49, 52, 57);
    padding-top: 1%;
    width: 100%;
    text-align: center;
    margin-bottom: -1%;
    animation: appear;
    animation-duration: 3s;
}

.container h3 {
    color: rgb(49, 52, 57);
    width: 100%;
    margin-top: 0;
    text-align: center;
    animation: appear;
    animation-duration: 5s;
    margin-bottom: 0%;
}

.orcamento {
    background-color: rgb(243, 243, 243);
    width: 100%;
}

.block {
    display: flex;
    padding-bottom: 1%;
    margin-bottom: 3%;
}

.list {
    width: 33.33%;
    text-align: center;
}

.list .title {
    color: rgb(146, 9, 9);
    font-size: 1.2em;
    font-weight: bolder;
}

.list button {
    background-color: rgb(146, 9, 9);
    border-color: rgb(146, 9, 9);
    border-radius: 60px;
    color: rgb(243, 243, 243);
}

.list button:hover {
    cursor: pointer;
}

.list button:active {
    cursor: wait;
}

.whats {
    background-color: rgb(27, 215, 65);
    position: fixed;
    bottom: 0.5em;
    left: 0.5em;
    height: 3em;
    width: 3em;
    border-radius: 100px;
    display: flex;
    justify-content: center;
    vertical-align: middle;
}

.whats img {
    height: 2em;
    padding-top: 0.45em;
}

.parceiros {
    background-color: rgb(243, 243, 243);
    height: 100%;
    margin-bottom: 3%;
    overflow: hidden;
}

.imgcontainer {
    display: flex;
}

.imagesback {
    width: 100%;
    margin-top: 3%;
}

.imagesfoard {
    width: 100%;
    margin-top: 3%;
}

.imagesfoard img {
    display: flex;
    animation: fromleft;
    animation-duration: 2s;
    width: 100%;
}

.imagesback img {
    display: flex;
    animation: fromright;
    animation-duration: 2s;
    width: 100%;
}

.imagesback div {
    width: 80%;
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    justify-content: center;
}

.imagesfoard div {
    width: 80%;
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    justify-content: center;
}

.parceiros button{
    margin-top: 5.5%;
    border-color: transparent;
    background-color: transparent;
    height: 100%;
    font-size: 1.5em;
}

.parceiros button:hover {
    cursor: pointer;
}

.next {
    margin-left: -3%;
}

@media (max-width: 1000px) {
    .container h1 {
        font-size: 1.5em;
    }

    .parceiros button {
        font-size: 1.2em;
    }
}

@media (max-width: 820px) {
    .list .title {
        font-size: 1em;
    }

    .list p {
        font-size: 80%;
    }

    .container h2 {
        font-size: 1.2em;
    }

    .container h3 {
        font-size: 0.8em;
    }

    .whats {
        height: 2em;
        width: 2em;
        left: 0.2em;
        bottom: 0.2em;
    }

    .whats img {
        height: 1.3em;
        padding-top: 0.35em;
    }
}

@media (max-width: 750px) {
    .container h1 {
        font-size: 1.1em;
    }

    .container h2 {
        font-size: 1.1em;
    }

    .list {
        width: 100%;
    }

    .block {
        display: block;
    }

    .parceiros button {
        font-size: 0.8em;
    }
}

@media (max-width: 490px) {
    .container h1 {
        font-size: 0.8em;
    }

    .container h2 {
        font-size: 0.8em;
    }

    .container h3 {
        font-size: 0.6em;
    }

    .list .title {
        font-size: 0.8em;
    }

    .list button {
        font-size: 0.5em;
    }

    .parceiros button {
        margin-top: 5%;
        font-size: 0.7em;
    }

    .whats {
        height: 1.5em;
        width: 1.5em;
        left: 0.2em;
        bottom: 0.2em;
    }

    .whats img {
        height: 1em;
        padding-top: 0.25em;
    }
}

@media (max-width: 400px) {
    .container h2 {
        font-size: 0.5em;
    }

    .container h3 {
        font-size: 0.4em;
    }

    .list .title {
        font-size: 0.5em;
        margin-top: 1em;
    }

    .list p {
        font-size: 0.5em;
    }

    .list button {
        font-size: 0.3em;
    }

    .whats {
        height: 1em;
        width: 1em;
        left: 0.25em;
        bottom: 0.25em;
    }

    .whats img {
        height: 0.7em;
        padding-top: 0.15em;
    }

    .parceiros button {
        margin-top: 4%;
        color: black;
    }
}