.textcontainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

.textcontainer h1 {
    width: 60%;
    text-align: center;
    font-style: italic;
}

.textcontainer h1.secondtext {
    margin-top: -3%;
}

.links {
    color: black;
    margin-right: 1%;
}

.services {
    width: 100%;
    height: 100%;
    color: rgb(49, 52, 57);
    background-color: rgb(243, 242, 242);
    display: flex;
    justify-content: center;
    margin-bottom: 5%;
}

.text {
    width: 60%;
    text-align: center;
    padding-left: 3%;
}

.images {
    width: 40%;
    height: 100%;
    text-align: center;
}

.images img {
    padding-top: 3%;
    width: 70%;
}

.images .pabx {
    width: 60%;
    height: 50%;
}

.images .video {
    width: 60%;
    height: 50%;
}

.images .nobreak {
    width: 60%;
    height: 50%;
}

.images .imgtwo {
    padding-top: 0;
    width: 70%;
}

.text button {
    background-color: rgb(228, 227, 227);
    border-color: rgb(210, 209, 209);
    color: rgb(120, 119, 119);
    border-radius: 10%;
    font-size: 1.5em;
    margin-top: -1%;
    margin-bottom: 3%;
}

.text button:hover {
    cursor: pointer;
}

.text .less {
    width: 4%;
    margin-top: 3%;
}

.text p {
    font-weight: bold;
}

.whats {
    background-color: rgb(27, 215, 65);
    position: fixed;
    bottom: 0.5em;
    left: 0.5em;
    height: 3em;
    width: 3em;
    border-radius: 100px;
    display: flex;
    justify-content: center;
    vertical-align: middle;
}

.whats img {
    height: 2em;
    padding-top: 0.45em;
}

@media (max-width: 1000px) {
    .images img {
        padding-top: 10%;
    }

    .images .imgtwo {
        padding-top: 10%;
    }

    .text .less {
        width: 5%;
        margin-top: 1%;
    }

    .images .video {
        width: 70%;
        height: 60%;
    }
}

@media (max-width: 900px) {
    .textcontainer h1 {
        font-size: 1.5em;
    }

    .text {
        font-size: 0.8em;
    }

    .images img {
        padding-top: 7.5%;
    }

    .images .imgtwo {
        padding-top: 0%;
    }
}

@media (max-width: 800px) {
    .images img {
        padding-top: 13%;
    }
    
    .images .imgtwo {
        padding-top: 13%;
    }

    .images .video {
        width: 70%;
        height: 60%;
        margin-top: 5%;
    }
}

@media (max-width: 750px) {
    .whats {
        height: 2em;
        width: 2em;
        left: 0.2em;
        bottom: 0.2em;
    }

    .whats img {
        height: 1.3em;
        padding-top: 0.35em;
    }
}

@media (max-width: 650px) {
    .textcontainer h1 {
        font-size: 1.2em;
    }

    .text {
        font-size: 0.6em;
    }

    .images .imgtwo {
        padding-top: 8%;
    }

    .text .less {
        width: 6.5%;
        margin-top: 1%;
    }

    .images .pabx {
        width: 60%;
        padding-top: 0;
    }

    .images .video {
        width: 80%;
        height: 70%;
        margin-top: 0;
    }
}

@media (max-width: 500px) {
    .textcontainer h1 {
        font-size: 1em;
    }

    .text {
        font-size: 0.5em;
    }

    .images img {
        width: 10em;
    }

    .images .imgtwo {
        width: 10em;
        padding-top: 8%;
    }

    .text .less {
        width: 7%;
        margin-top: 1%;
    }

    .images .pabx {
        padding-top: 3%;
        width: 70%;
    }

    .images .nobreak {
        width: 70%;
    }
}

@media (max-width: 490px) {
    .whats {
        height: 1.5em;
        width: 1.5em;
        left: 0.2em;
        bottom: 0.2em;
    }

    .whats img {
        height: 1em;
        padding-top: 0.25em;
    }
}

@media (max-width: 400px) {
    .images img {
        width: 9em;
        height: 8em;
    }

    .images .imgtwo {
        width: 9em;
        padding-top: 8%;
    }   

    .text .less {
        width: 9%;
        margin-top: 1%;
    }
    
    .images .pabx {
        width: 80%;
        height: 80%;
        padding-top: 5%;
    }

    .images .nobreak {
        margin-top: 5%;
        width: 80%;
        height: 80%;
    }

    .images .video {
        width: 100%;
        height: 100%;
        margin-top: 13%;
    }

    .whats {
        height: 1em;
        width: 1em;
        left: 0.25em;
        bottom: 0.25em;
    }

    .whats img {
        height: 0.7em;
        padding-top: 0.15em;
    }
}