.footer {
    background-color: rgb(49, 52, 57);
    width: 100%;
    height: 100%;
}

.textcontainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

.firsttext {
    width: 25%;
    padding-top: 1%;
}

.secondtext {
    width: 30%;
    padding-top: 1%;
}

.thirdtext {
    width: 30%;
    padding-top: 1%;
}

.footer h1 {
    width: 80%;
    text-align: center;
    padding-left: 10%;
    padding-right: 10%;
    margin-top: 3%;
    margin-bottom: 3%;
    animation: none;
    color: white;
    font-size: 1em;
    margin-bottom: -3%;
}

.firsttext h2 {
    animation: none;
    color: rgb(201, 200, 200);
    font-size: 0.8em;
    margin-left: 25%;
    padding-top: 1%;
    width: 69%;
    text-align: start;
}

.secondtext h2 {
    animation: none;
    color: rgb(201, 200, 200);
    font-size: 0.8em;
    padding-top: 1%;
    margin-left: 33%;
    width: 69%;
    text-align: start;
}

.thirdtext h2 {
    animation: none;
    color: rgb(201, 200, 200);
    font-size: 0.8em;
    margin-left: 34.5%;
    width: 69%;
    padding-top: 1%;
    text-align: start;
}

.footer .links {
    color: rgb(201, 200, 200);
}

.linecontainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

.line {
    height: 1px;
    color: rgb(201, 200, 200);
    width: 90%;
    margin-top: 3%;
    margin-bottom: 3%;
}

.final {
    width: 100%;
    display: flex;
}

.finalleft {
    display: block;
    width: 47%;
}

.finalleft h3 {
    color: white;
    font-size: 3em;
    width: 100%;
    margin-top: 0;
    text-align: center;
    margin-bottom: 0%;
    animation: none;
}

.finalleft h4 {
    color: rgb(201, 200, 200);
    font-size: 0.8em;
    padding-left: 28%;
    margin-top: -1%;
}

.finalright {
    width: 53%;
    display: flex;
    justify-content: flex-end;
}

.finalright img {
    width: 11em;
    padding-right: 6em;
    margin-top: -1em;
}

@media (max-width: 1000px) {
    .firsttext {
        width: 30%;
    }

    .firsttext h2 {
        margin-left: 22%;
    }

    .secondtext h2 {
        margin-left: 27%;
    }

    .thirdtext h2 {
        margin-left: 27%;
    }

    .finalleft h4 {
        padding-left: 22%;
    }

    .finalright img {
        margin-top: -0.8em;
    }
}

@media (max-width: 800px) {
    .footer h1 {
        font-size: 0.8em;
    }

    .footer h2 {
        font-size: 0.6em;
    }

    .finalleft h3 {
        font-size: 2em;
    }
    
    .finalleft h4 {
        font-size: 0.6em;
        padding-left: 26%;
        margin-top: 0%;
    }

    .finalright img {
        width: 7em;
        padding-right: 20%;
        margin-top: -0.4em;
    }
}

@media (max-width: 650px) {

    .firsttext {
        width: 33.33%;
    }

    .secondtext {
        width: 33.33%;
    }

    .thirdtext {
        width: 33.33%;
    }

    .firsttext h2 {
        margin-left: 16%;
    }

    .secondtext h2 {
        margin-left: 23%;
    }

    .thirdtext h2 {
        margin-left: 24%;
    }

    .finalleft h3 {
        font-size: 1.5em;
    }
    
    .finalleft h4 {
        font-size: 0.5em;
        padding-left: 28%;
        margin-top: 0%;
    }

    .finalright img {
        width: 6em;
        padding-right: 20%;
    }
}

@media (max-width: 540px) {
    .footer h1 {
        font-size: 0.6em;
    }

    .footer h2 {
        font-size: 0.45em;
    }

    .firsttext h2 {
        margin-left: 22%;
    }

    .secondtext h2 {
        margin-left: 27%;
    }

    .thirdtext h2 {
        margin-left: 29%;
    }

    .finalleft h3 {
        font-size: 1.2em;
    }
    
    .finalleft h4 {
        font-size: 0.4em;
        padding-left: 30%;
        margin-top: 0%;
    }

    .finalright img {
        width: 5em;
        padding-right: 20%;
        margin-top: -0.2em;
    }
}

@media (max-width: 400px) {

    .footer h1 {
        font-size: 0.5em;
    }

    .footer h2 {
        font-size: 0.4em;
    }

    .firsttext h2 {
        margin-left: 19%;
    }

    .secondtext h2 {
        margin-left: 24%;
    }

    .thirdtext h2 {
        margin-left: 26%;
    }

    .finalleft h3 {
        font-size: 1em;
    }
    
    .finalleft h4 {
        font-size: 0.1em;
        padding-left: 24%;
        margin-top: 0%;
    }

    .finalright img {
        width: 4em;
        padding-right: 20%;
        margin-top: 0;
    }
}