@keyframes labelappear {
    from {
        opacity: 0;
        margin-bottom: -5%;
    }

    to {
        opacity: 1;
        margin-bottom: 0%;
    }
}

@keyframes messageappear {
    0% {
        opacity: 0;
        right: -40em;
    }

    25% {
        opacity: 1;
        right: 2.5em;
    }

    75% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.container {
    width: 100%;
    background-color: rgb(223, 223, 223);
    text-align: center;
    color: rgb(146, 9, 9);
    font-style: italic;
    height: 100%;
    margin-top: 5%;
    margin-bottom: 5%;
}

.container h1 {
    font-weight: bolder;
    font-size: 3em;
    margin-bottom: -2%;
    padding-top: 3%;
}

.container h2 {
    font-weight: lighter;
    margin-bottom: 3%;
}

.block {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.firstbox {
    background-color: white;
    width: 30%;
    max-height: 500px;
    min-height: 500px;
    border-radius: 29px;
    box-shadow: 6px 6px 37px #707070,
        -6px -6px 37px #ffffff;
    margin-bottom: 5%;
}

.firstbox img {
    margin-top: 5%;
    width: 80%;
    height: 80%;
}

.links {
    margin-top: 5%;
    margin-bottom: 5%;
}

.links .insta {
    width: 10%;
}

.links .whats {
    width: 10%;
    margin-left: 5%;
    margin-right: 5%;
}

.links .mail {
    width: 10%;
}

.firstbox h3 {
    font-style: normal;
    color: rgb(49, 52, 57);
}

.secondbox {
    background-color: white;
    width: 50%;
    margin-left: 2%;
    border-radius: 29px;
    max-height: 500px;
    min-height: 500px;
    box-shadow: 6px 6px 37px #707070,
        -6px -6px 37px #ffffff;
    margin-bottom: 5%;
}

.secondbox h3 {
    font-size: 1.5em;
    font-style: normal;
    font-weight: bolder;
    color: rgb(49, 52, 57);
    margin-bottom: -3%;
}

.secondbox h4 {
    font-size: 1em;
    font-style: normal;
    font-weight: lighter;
    color: rgb(49, 52, 57);
}

.formcontainer {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 5%;
}

.text {
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1.5px solid rgb(49, 52, 57);
    font-size: 1.2em;
    font-family: 'Times New Roman', Times, serif;
    height: 2em;
    width: 70%;
}

.textarea {
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1.5px solid rgb(49, 52, 57);
    font-size: 1.2em;
    height: 5em;
    width: 70%;
    resize: none;
    font-family: 'Times New Roman', Times, serif;
}

.labelcontainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

.label {
    display: flex;
    width: 70%;
    text-align: start;
    font-style: normal;
    color: rgb(49, 52, 57);
    animation: labelappear;
    animation-duration: 1s;
    font-size: 1em;
}

.submit {
    width: 30%;
    height: 2.5em;
    border-radius: 10px;
    font-size: 1em;
    font-weight: bolder;
    background-color: rgb(197, 197, 197);
    color: rgb(49, 52, 57);
}

.success {
    color: green;
    padding-top: 0.5%;
    font-size: 0.5em;
    text-align: center;
    width: 20%;
    border-radius: 10px;
    position: fixed;
    top: 2.5em;
    right: 2.5em;
    opacity: 0;
    background-color: white;
    border: 3px solid green;
    animation: messageappear;
    animation-duration: 4s;
}

.error {
    color: red;
    padding-top: 0.5%;
    font-size: 0.5em;
    text-align: center;
    width: 30%;
    border-radius: 10px;
    position: fixed;
    top: 2.5em;
    right: 2.5em;
    opacity: 0;
    background-color: white;
    border: 3px solid red;
    animation: messageappear;
    animation-duration: 4s;
}

.loader {
    border: 5px solid #f3f3f3;
    border-top: 5px solid rgb(49, 52, 57);
    border-radius: 100%;
    width: 30px;
    height: 30px;
    animation: spin 1s linear infinite;
}

.formcontainertwo {
    width: 100%;
    display: flex;
    justify-content: center;
}

input:focus {
    outline-width: 0;
}

textarea:focus {
    outline-width: 0;
}

input[type='submit']:hover {
    cursor: pointer;
}

