@keyframes display {
    12.5% {
        margin-left: 0%;
    }

    25% {
        margin-left: -100%;
    }

    37.5% {
        margin-left: -100%;
    }

    50% {
        margin-left: -200%;
    }

    62.5% {
        margin-left: -200%;
    }

    75% {
        margin-left: -100%;
    }

    87.5% {
        margin-left: -100%;
    }

    100% {
        margin-left: 0%;
    }
}

@keyframes automaticButtonOne {
    0% {
        background-color: rgb(161, 153, 153);
    }

    20% {
        background-color: rgb(161, 153, 153);
    }

    25% {
        background-color: #eae6e6;
    }

    95% {
        background-color: #eae6e6;
    }

    100% {
        background-color: rgb(161, 153, 153);
    }
}

@keyframes automaticButtonThwo {
    20% {
        background-color: #eae6e6;
    }

    25% {
        background-color: rgb(161, 153, 153);
    }

    45% {
        background-color: rgb(161, 153, 153);
    }

    50% {
        background-color: #eae6e6;
    }

    70% {
        background-color: #eae6e6;
    }

    75% {
        background-color: rgb(161, 153, 153);
    }

    95% {
        background-color: rgb(161, 153, 153);
    }

    100% {
        background-color: #eae6e6;
    }
}

@keyframes automaticButtonThree {
    45% {
        background-color: #eae6e6;
    }

    50% {
        background-color: rgb(161, 153, 153);
    }

    70% {
        background-color: rgb(161, 153, 153);
    }

    75% {
        background-color: #eae6e6;
    }
}

.images {
    display: flex;
    width: 100%;
}

.images img {
    width: 100%;
    flex-shrink: 0;
}

.automatic {
    animation: display;
    animation-duration: 16s;
    animation-iteration-count: infinite;
}

.manualtwo {
    margin-left: -100%;
}

.manualthree {
    margin-left: -200%;
}

.dots {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: -1.5%;
}

.dots button {
    height: 15px;
    width: 15px;
    margin: 0 2px;
    border: transparent;
    border-radius: 50%;
    display: inline-block;
}

.dots button:hover {
    cursor: pointer;
}

.dotone {
    animation: automaticButtonOne;
    animation-duration: 16s;
    animation-iteration-count: infinite;
}

.dottwo {
    animation: automaticButtonThwo;
    animation-duration: 16s;
    animation-iteration-count: infinite;
}

.dotthree {
    animation: automaticButtonThree;
    animation-duration: 16s;
    animation-iteration-count: infinite;
}

.dotactive {
    background-color: rgb(161, 153, 153);
}

.dotunactive {
    background-color: #eae6e6;
}

.text {
    margin-top: 5%;
    margin-bottom: 10%;
    background-color: rgb(182, 182, 182);
    color: rgb(49, 52, 57);
    width: 100%;
    height: 100%;
    display: flex;
}

.textcontainer {
    display: flex;
    width: 100%;
    justify-content: center;
}

.lefttext {
    width: 70%;
    text-align: center;
}

.textcontainer h2 {
    margin-top: -2%;
    font-size: 1.2em;
    width: 55%;
}

.links {
    color: rgb(49, 52, 57);
}

.image{
    width: 30%;
    display: flex;
    justify-content: center;
}

.image img {
    width: 90%;
    height: 90%;
    margin-right: 50%;
    padding-top: 3%;
}

.imageleft {
    width: 30%;
    display: flex;
    justify-content: center;
}

.imageleft img {
    width: 80%;
    height: 90%;
    padding-top: 3%;
    margin-left: 50%;
}

.whats {
    background-color: rgb(27, 215, 65);
    position: fixed;
    bottom: 0.5em;
    left: 0.5em;
    height: 3em;
    width: 3em;
    border-radius: 100px;
    display: flex;
    justify-content: center;
    vertical-align: middle;
}

.whats img {
    height: 2em;
    padding-top: 0.45em;
}

@media (max-width: 1500px) {
    .dots {
        margin-top: -2%;
    }

    .textcontainer h2 {
        margin-top: -2%;
        font-size: 1.2em;
        width: 70%;
    }

    .image img {
        padding-top: 5%;
    }
}

@media (max-width: 900px) {
    .dots {
        margin-top: -2.5%;
    }

    .dots button {
        height: 13px;
        width: 13px;
    }

    .textcontainer h2 {
        font-size: 1em;
        width: 70%;
    }

    .lefttext h1 {
        font-size: 1.5em;
    }

    .image img {
        padding-top: 5%;
        margin-right: 30%;
    }

}

@media (max-width: 820px) {
    .textcontainer h2 {
        font-size: 1em;
        width: 80%;
    }

    .lefttext h1 {
        font-size: 1.5em;
    }

    .image img {
        height: 80%;
        padding-top: 13%;
        margin-right: 20%;
    }
}

@media (max-width: 750px) {
    .whats {
        height: 2em;
        width: 2em;
        left: 0.2em;
        bottom: 0.2em;
    }

    .whats img {
        height: 1.3em;
        padding-top: 0.35em;
    }
}

@media (max-width: 580px) {
    .dots {
        margin-top: -3.5%;
    }

    .dots button {
        height: 11px;
        width: 11px;
    }

    .textcontainer h2 {
        font-size: 0.8em;
        width: 80%;
    }

    .lefttext h1 {
        font-size: 1.2em;
    }

    .image img {
        height: 80%;
        width: 100%;
        padding-top: 15%;
        margin-right: 20%;
    }

    .imageleft img {
        width: 100%;
        height: 90%;
        padding-top: 4%;
        margin-left: 25%;
    }
}

@media (max-width: 490px) {
    .whats {
        height: 1.5em;
        width: 1.5em;
        left: 0.2em;
        bottom: 0.2em;
    }

    .whats img {
        height: 1em;
        padding-top: 0.25em;
    }
}

@media (max-width: 400px) {
    .dots {
        margin-top: -5%;
        margin-bottom: 7%;
        height: 100%;
    }

    .dots button {
        height: 10px;
        width: 10px;
        padding: 0;
        border-radius: 100%;
    }

    .textcontainer h2 {
        font-size: 0.6em;
        width: 80%;
    }

    .lefttext h1 {
        font-size: 0.8em;
    }

    .image img {
        height: 80%;
        width: 120%;
        padding-top: 18%;
        margin-right: 20%;
    }
}