.head {
    background-color: rgb(49, 52, 57);
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
}

.insta {
    height: 1.3em;
    padding-top: 0.8em;
}

.whats {
    height: 1.38em;
    padding-top: 0.75em;
}

.whatsref {
    margin-left: 1%;
}

.mail {
    font-size: 1.1em;
    text-decoration: none;
    color: rgb(204, 205, 207);  
}

.mail p {
    margin-top: 0.6em;
    margin-bottom: 0;
}

.phone {
    height: 1.3em;
    margin-top: 0.8em;
    margin-right: 0.8%;
}

.phonetxt {
    font-size: 1.1em;
    color: rgb(204, 205, 207);
    margin-top: 0.7em;
}

.first {
    display: flex;
    width: 20%;
    justify-content: center;
}

.second {
    display: flex;
    width: 50%;
    justify-content: center;
}

.third {
    display: flex;
    width: 30%;
    justify-content: center;
}

.nav {
    background-color: rgb(223, 223, 223);
    height: 100%;
    padding: 0.5%;
    display: flex;
}

.nav a:link {
    text-decoration: none;
}

.navstick {
    background-color: rgb(223, 223, 223);
    height: 4em;
    padding: 0.02em;
    display: flex;
    position: fixed;
    top: 0px;
    width: 100%;
}

.navstick a:link {
    text-decoration: none;
}

.navFirst {
    width: 25%;
    display: flex;
    justify-content: center;
    font-size: 3em;
    font-weight: bold;
    font-style: italic;
}

.fcp {
    color: rgb(49, 52, 57);
}

.services {
    color: rgb(146, 9, 9);
}

.navSecond {
    width: 75%;
    display: flex;
}

.links {
    display: flex;
    width: 25%;
    padding-top: 1%;
    justify-content: center;
    font-size: 2em;
    font-weight: bold;
    color: rgb(49, 52, 57);
}

@media (max-width: 1000px) {
    .navFirst{
        font-size: 2.3em;
    }

    .links{
        font-size: 1.5em;
    }

    .navstick{
        height: 2.9em;
    }
}

@media (max-width: 800px) {
    .navFirst{
        font-size: 1.8em;
        padding-top: 0.8%;
    }

    .links{
        font-size: 1.2em;
        padding-top: 2.5%;
    }

    .navstick{
        height: 2.7em;
    }
}

@media (max-width: 600px){
    .head {
        height: 100%;
        display: block;
    }

    .first{
        width: 100%;
    }

    .second{
        width: 100%;
    }

    .third{
        width: 100%;
        margin-top: 0.3em;
    }

    .navFirst{
        font-size: 1.3em;
        padding-top: 1.2%;
    }

    .links{
        font-size: 0.8em;
        padding-top: 3%;
    }

    .navstick {
        height: 2.3em;
    }
}

@media (max-width: 450px){
    .navFirst{
        font-size: 1em;
    }

    .links{
        font-size: 0.6em;
    }

    .insta{
        height: 1em;
    }

    .whats{
        height: 1.1em;
    }

    .mail{
        font-size: 0.8em;
    }

    .phone{
        height: 1em;
        margin-top: 0.55em;
    }

    .phonetxt{
        font-size: 0.8em;
    }

    .navstick {
        height: 1.6em;
    }
}