.textcontainer {
    width: 100%;
    display: flex;
    justify-content: center;
    vertical-align: middle;
}

.textcontainer h1 {
    width: 60%;
    text-align: center;
    font-style: italic;
}

.imagecontainer {
    display: flex;
    width: 100%;
    height: 100%;
    padding-top: 3%;
    padding-bottom: 3%;
    justify-content: center;
    background-color: rgb(243, 242, 242);
}

.image {
    width: 70%;
}

.links {
    color: black;
    margin-right: 1%;
}

.whats {
    background-color: rgb(27, 215, 65);
    position: fixed;
    bottom: 0.5em;
    left: 0.5em;
    height: 3em;
    width: 3em;
    border-radius: 100px;
    display: flex;
    justify-content: center;
    vertical-align: middle;
}

.whats img {
    height: 2em;
    padding-top: 0.45em;
}

@media (max-width: 900px) {
    .textcontainer h1 {
        font-size: 1.5em;
    }
}

@media (max-width: 750px) {
    .whats {
        height: 2em;
        width: 2em;
        left: 0.2em;
        bottom: 0.2em;
    }

    .whats img {
        height: 1.3em;
        padding-top: 0.35em;
    }
}

@media (max-width: 650px) {
    .textcontainer h1 {
        font-size: 1.2em;
    }
}

@media (max-width: 500px) {
    .textcontainer h1 {
        font-size: 1em;
    }
}

@media (max-width: 490px) {
    .whats {
        height: 1.5em;
        width: 1.5em;
        left: 0.2em;
        bottom: 0.2em;
    }

    .whats img {
        height: 1em;
        padding-top: 0.25em;
    }
}

@media (max-width: 400px) {
    .whats {
        height: 1em;
        width: 1em;
        left: 0.25em;
        bottom: 0.25em;
    }

    .whats img {
        height: 0.7em;
        padding-top: 0.15em;
    }
}